import React, { useRef, useState } from "react";
import {select, selectAll} from 'd3-selection'

function SearchBar(props){
    const { data, setClickedIdx } = props;
    const {names} = data;
    const parsedNames = names.map( (t,i) => {
        t["idx"] = i;
        return t
    });

    const inputRef = useRef();
    const listRef = useRef();
    const parsedNames_sorted = parsedNames.sort(function(a, b) {
        if (a.name_sort < b.name_sort) {
            return -1;
        }
        if (a.name_sort > b.name_sort) {
            return 1;
        }
        return 0;
    });
    let [searchingNames, setSearchingNames] = useState(parsedNames_sorted);

    function onClick(event){
        let currentList = listRef.current;
        currentList.classList.add('show')
        
    }

    function onItemClicked(event){
        select(".tooltip_ctn__100").classed("newActive", false);
        const name = event.target.innerHTML;
        const idx = event.target.dataset.idx;
        inputRef.current.value = name;
        select("#dropDownList_names").classed('show', false);
        setClickedIdx(clickedIdx => idx);
        
        // hide the .step__outer__9
        select(".step__outer__9").classed('active', false);

        const audioFile = document.querySelector(`#chime-${(idx % 12) + 1}`);
        audioFile.currentTime = 0;
        audioFile.volume = 0.3 + Math.random() * 0.6;
        audioFile.play();
    }

    function onKeyUp(event){
        console.log('onKeyUp: ', event, event.target.value)
        const val = event.target.value;
        filterFunction(val);
    }

    function filterFunction(val) {
        let filter = val.toUpperCase();
        setSearchingNames((searchingNames)=>{
            let filteredNames = parsedNames.filter(
                t => (t.name_display +', '+ t.county).toUpperCase().indexOf(filter) > -1
            );
            return filteredNames.sort(function(a, b) {
                if (a.name_sort < b.name_sort) {
                    return -1;
                }
                if (a.name_sort > b.name_sort) {
                    return 1;
                }
                return 0;
            });
        })
        let a = document.getElementsByClassName(".list-item");
        for (let i = 0; i < a.length; i++) {
            if (a[i].innerHTML.toUpperCase().indexOf(filter) > -1) {
                a[i].style.display = "";
            } else {
                a[i].style.display = "none";
            }
            a[0].style.display = "";
        }
    }

    return (
        <div className="search-bar-ctn">
            <h5>Search by name or county</h5>
            <input
                ref={inputRef}
                type="text"
                placeholder="Search..."
                id="search-bar-input"
                onClick={onClick}
                onKeyUp={onKeyUp}
                autoComplete="off"
            />
            <div ref={listRef} className="dropDownList" id="dropDownList_names">
                <div className="dropdown-content">
                    {searchingNames.map((t, i) => (
                        <a
                            className="list-item"
                            data-idx={t.idx}
                            key={t.id}
                            id={t.id}
                            onClick={onItemClicked}
                        >
                            {t.name_display+', '+t.county }
                        </a>
                    ))}
                </div>
            </div>
        </div>
    );
}

export default SearchBar