import React, { useRef } from "react";
import { useFrame, useLoader } from "@react-three/fiber";
import { RainShader } from "./RainShader";
import { TextureLoader } from "three/src/loaders/TextureLoader";
import { stepPoint } from "./data.js";
import { AdditiveBlending } from "three";
import { death_to_date, newDeathsPerWeek } from "./data";
import pointPosByCounty from '../data/pointByCountyPos_random.json';
function Points(props) {
    // This reference will give us direct access to the THREE.Mesh object
    let { step, uNowTime, COUNT, startTime, randomNumArr, isMobile } = props;
    // console.log("Points COUNT: ", COUNT);
    const stepNum = !stepPoint[step] ? 0.0 : stepPoint[step]["num"];
    const highlightArr =
        step && step > 2
            ? [
                  stepPoint[step]["num"] - 1,
                  stepPoint[step]["num"] - 2,
                  stepPoint[step]["num"] - 3
              ]
            : [];

    const lastStepNum = !step || step < 1 ? 0.0 : stepPoint[step - 1]["num"];

    console.log(
        "points stepNum, lastStepNum",
        step,
        stepNum,
        lastStepNum,
        highlightArr
    ); // 1,1,0
    const mesh = useRef();
    const mat = useRef();
    // Set up state for the hovered and active state
    let hoverIdx = 0;
    const [pointTexture] = useLoader(TextureLoader, [
        "./assets/images/raindrop_img.png"
    ]);

    // Subscribe this component to the render-loop, rotate the mesh every frame
    useFrame((state, delta) => {
        const rainDrop = mesh.current;
        const material = mat.current;
        if (rainDrop) {
            // update uTime, uOldTime
            // rainDrop.rotation.y += 0.0001;
            material.uniforms.uTime.value = state.clock.elapsedTime;
            material.uniforms.uOldTime.value = state.clock.oldTime;
            material.uniforms.uHoverIdx.value = hoverIdx;
            rainDrop.geometry.attributes.position.needsUpdate = true;
            rainDrop.geometry.computeBoundingBox();
            // uTime: time since the page is loaded the first time;
            // uOldTime-startTime: recount on ever step
            // console.log("material.uniforms.uTime", material.uniforms.uTime, material.uniforms.uStartTime, material.uniforms.uOldTime);
        }
    });

    // Return the view, these are regular Threejs elements expressed in JSX
    const vertices = [],
        aSpeed = [],
        aRandom = [];
    vertices.push(0.0, 100.0, 0.0); // vec3( 400.* (aRandom.x-.5), -50.-200.*aRandom.y, 0.);
    aSpeed.push(0.001, 0, 0); // index, rank, newDeathsPerWeek
    aRandom.push(0.5, 0.4);

    //TODO: use React.useMemo?
    for (let i = 1; i < COUNT; i++) {
        let R = i < 11 ? 200 : 400;
        // const x = (Math.random() * 100 - 50) * Math.sin(Math.random());
        // const y = Math.random() * 400 - 200;
        // const z = (Math.random() * 50 - 25) * Math.cos(Math.random());
        let t = 2 * Math.PI * randomNumArr[i];
        let u =
            randomNumArr[i + Math.round(COUNT / 500.0) - 1] +
            randomNumArr[i + Math.round(COUNT / 250.0) - 1];
        let r = u > 1 ? (2 - u) * R : u * R;

        const x = r * Math.cos(t);
        const y = r * Math.sin(t);
        const z = randomNumArr[i] * 200 - 100;

        let whichWeek = getRank(i, death_to_date);

        vertices.push(x, y, z);
        aSpeed.push(i, whichWeek, newDeathsPerWeek[whichWeek]);
        aRandom.push(
            i < 11 ? getOneThirdPosX(i) : randomNumArr[i],
            i < 11
                ? getOneThirdPosY(i)
                : randomNumArr[i + Math.floor(COUNT / 500)]
        );
    }

    if (pointPosByCounty.length!==vertices.length){
        console.log(
            "aEndPosition doesn't have enough data. update ../data/pointByCountyPos_random.json file. ",
            vertices.length,
            pointPosByCounty.length
        );
    }


    function getOneThirdPosX(i) {
        if (i % 3 === 1) { // vec3 endPoint = vec3( 400.* (aRandom.x-.5), -50.-200.*aRandom.y, 0.);
            // -220, -40, 120
            return -0.05;
        } else if (i % 3 == 2) {
            return 0.4;
        } else if (i % 3 == 0) {
            return 0.8;
        }
    }
    function getOneThirdPosY(i) {
        if (Math.floor((i - 1) / 3) === 0) {
            return 0.1;
        } else if (Math.floor((i - 1) / 3) === 1) {
            return 0.4;
        } else if (Math.floor((i - 1) / 3) === 2) {
            return 0.7;
        }
    }
    function getRank(idx, arr) {
        let rank = 0;
        for (let i = 0; i < arr.length - 1; i++) {
            if (idx >= arr[i] && idx < arr[i + 1]) {
                rank = arr.indexOf(arr[i + 1]);
            }
        }
        return rank;
    }

    return (
        <points ref={mesh}>
            <bufferGeometry attach="geometry" name="unclickable-point-geo">
                <bufferAttribute
                    attachObject={["attributes", "position"]}
                    count={vertices.length / 3}
                    // this renders the dots fine
                    array={new Float32Array(vertices)}
                    itemSize={3}
                    onUpdate={self => {
                        self.needsUpdate = true;
                        self.verticesNeedUpdate = true;
                    }}
                />
                <bufferAttribute
                    attachObject={["attributes", "aEndPosition"]}
                    count={vertices.length / 3}
                    // this renders the dots fine
                    array={new Float32Array(pointPosByCounty)}
                    itemSize={3}
                    onUpdate={self => {
                        self.needsUpdate = true;
                        self.verticesNeedUpdate = true;
                    }}
                />
                <bufferAttribute
                    attachObject={["attributes", "aSpeed"]}
                    count={aSpeed.length / 3}
                    array={new Float32Array(aSpeed)}
                    itemSize={3}
                />
                <bufferAttribute
                    attachObject={["attributes", "aRandom"]}
                    count={aRandom.length / 2}
                    array={new Float32Array(aRandom)}
                    itemSize={2}
                />
            </bufferGeometry>

            <shaderMaterial
                attach="material"
                transparent={true}
                depthTest={false}
                ref={mat}
                args={[RainShader]}
                blending={AdditiveBlending}
                uniforms-uTime={{ value: 0 }}
                uniforms-uOldTime={{ value: 0 }}
                uniforms-uNowTime={{ value: uNowTime }}
                uniforms-uStartTime={{ value: startTime }}
                uniforms-uHoverIdx={{ value: hoverIdx }}
                uniforms-pointTexture={{ value: pointTexture }}
                uniforms-uLastHighlight={{
                    value: !step || step < 1 ? 0.0 : stepPoint[step - 1]["num"]
                }}
                uniforms-uIsMobile={{value: (isMobile?1:0) }}
                uniforms-uHighlight={{ value: stepNum }}
            />
        </points>
    );
}

export default React.memo(Points);
