import * as THREE from 'three';
import React, { Suspense, useRef, useEffect } from "react";
import { Canvas, useThree, useFrame } from "@react-three/fiber";
import Points from "./Points";
import Cloud from "./Cloud";
import FlashLight from "./FlashLight";
import {dateArr} from './data.js';
// import { FlyControls } from "three/examples/jsm/controls/FlyControls";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import Map from './Map'

// import {
//     EffectComposer,
//     BlendMode,
//     DepthOfField,
//     Bloom,
//     Noise,
//     Vignette
// } from "@react-three/postprocessing";

import { GizmoHelper, GizmoViewport } from "@react-three/drei";
// https://docs.pmnd.rs/react-three-fiber/API/objects
import UpdateCamera from './UpdateCamera';
import PointsClickable from './PointsClickable';


const CameraController = (props) => {
    const { camera, gl, scene } = useThree();
    // gl.format = RGBAformat;
    useEffect(() => {
        const controls = new OrbitControls(camera, gl.domElement);
        controls.minDistance = 20;
        controls.maxDistance = 1000;
        return () => {
            controls.dispose();
        };
    }, [camera, gl]);
    

    return null;
};

function Scene(props){    
    const { step, COUNT, randomNumArr, startTime, clickedIdx, deathData, pointByCountyPosWithName, isMobile } = props; // step
    // set up camera init here
    console.log('isMobile: ', isMobile);
    const cameraInitPos = isMobile
        ? [0, -100, 600]
        : [0, 0, 500];
    return (
        <Canvas
            camera={{ position: cameraInitPos, far: 1000, near: 0.1 }}
            gl={{
                format: THREE.RGBAFormat,
                outputEncoding: THREE.sRGBEncoding
            }}
        >
            <mesh name={"cameraTarget"} visible={false} position={[0, 0, 0]}>
                <boxGeometry args={[1, 1, 1]} />
                <meshBasicMaterial color="black" />
            </mesh>
            <UpdateCamera step={step} isMobile={isMobile} />
            <FlashLight />
            <ambientLight color="#555555" />
            {
                (step > 10) &&
                <CameraController />
            }

            {
            step < 11 &&
                [1, 3, 4, 5, 6, 7, 8, 9, 10].map((t, i) => {
                    if (step > t) {
                        return (
                            <Text
                                key={`axis-label-text-${t}`}
                                color="#e2e3e4"
                                fontSize={10}
                                anchorX={250 - 60 * i -(isMobile && (i===0)? 14:0 ) }
                                anchorY={255+ (isMobile? 85:0) }
                            >
                                {dateArr[i]}
                            </Text>
                        );
                    }
                    return null;
                })}

            <Suspense fallback={null}>
                <Points
                    step={step}
                    uNowTime={performance.now()}
                    COUNT={COUNT}
                    randomNumArr={randomNumArr}
                    startTime={startTime}
                    isMobile={isMobile}
                />
                {/* {step == 11 && <Map />} */}
                <Map
                    opacity={step > 10 ? 0.6 : 0.3}
                    scale={!step || step <= 10 ? [0.5, 0.5, 0.5] : [1, 1, 1]}
                />
                <Cloud
                    position={[0, 225, -200]}
                    rotation={[1.26, -0.12, 250]}
                />
                <Cloud
                    position={[220, 200, -100]}
                    rotation={[1.16, -0.2, 250]}
                />
                <Cloud
                    position={[-220, 210, -50]}
                    rotation={[1.46, 0.1, 220]}
                />
                {
                step > 10 && 
                    <PointsClickable
                        nowTime={performance.now()}
                        // startTime={startTime}
                        clickedIdx={clickedIdx}
                        deathData={deathData}
                        pointByCountyPosWithName={pointByCountyPosWithName}
                    />
                }
            </Suspense>
        </Canvas>
    );
}


export default Scene;
