import React, { Component } from 'react'
import Main from "./Main"
import Navbar from "./Navbar"
import { isIE } from 'react-device-detect';
class App extends Component {
    render() {
        if (isIE)
            return (
                <div> IE is not supported. Download Chrome/Opera/Firefox </div>
            );
            
        return(
            <div className="App">
                    <Navbar />
                    <Main />
            </div>
        )
    }
}

export default App