import React, { useEffect, memo } from "react";
import {selectAll} from 'd3-selection';

function Tooltip(props) {
    let { data, stepIndex, currentStepIndex, pos, count } = props;
   
    const {name, age, location, county } = data;
    useEffect(() => {
        selectAll(".tooltip_ctn").classed("active", true);
    }, [currentStepIndex]);
    return (
        <div
            className={`tooltip_ctn tooltip_ctn__${stepIndex}`}
            data-step={stepIndex}
            key={`tooltip_ctn__${stepIndex}`}
            data-posx = {pos.x}
            data-posy = {pos.y}
            data-count = {count}
            style={{ position: "absolute", top: "-50%", left: "-50%", color:'#ababab', animationDelay:`${ count* (currentStepIndex==2? 0.2: 0.3)}s` }}
        >
            <p className="tooltip tooltipHeader">{name}</p>
            
            <p className="tooltip tooltipLoc" id={`tooltip__loc__${stepIndex}`}>
                {`${age}, ${location? location: county+" County" }`}
            </p>
        </div>
    );
}

export default memo(Tooltip);
