import React from "react";
import { useLoader } from "@react-three/fiber";
import { TextureLoader } from "three/src/loaders/TextureLoader";
import { DoubleSide } from "three";
function Map(props) {
    const {opacity, scale} = props;
    // Draw map here: https://observablehq.com/d/70bfdb7d8450275d
    const [colorMap] = useLoader(TextureLoader, [
        "./assets/images/nj_map4.png"
    ]);

    return (
        <mesh position={[0, -100, 0]} rotation={[-1.57, 0, 0]} scale={scale}>
            <planeBufferGeometry args={[686, 1407]} />
            <meshBasicMaterial
                opacity={opacity}
                // side={DoubleSide}
                transparent={true}
                map={colorMap}
                depthTest={false}
            />
        </mesh>
    );
}

export default Map;
