// import { Box} from "@react-three/drei";
import React from "react";
import { useThree, useFrame } from "@react-three/fiber";
import TWEEN from "@tweenjs/tween.js";
import * as THREE from "three";
import worldToScreen from './worldToScreen';

const UpdateCamera = props => {
    const { step, isMobile } = props;
    console.log('updateCamera-step: ', step)

    let theta = 0;
    const { camera, gl, scene } = useThree();
    camera.fov = isMobile? 75: 60;
    camera.near = 0.1;
    camera.far = 2000;
    
    let allTooltips = document.querySelectorAll(".tooltip_ctn");
    allTooltips &&
        allTooltips.forEach(t => {
            let posX = +t.dataset.posx;
            let posY = +t.dataset.posy;
            let newPos = worldToScreen(
                new THREE.Vector3(posX, posY, 0),
                gl,
                camera
            );
            t.style.left = `calc(${(newPos.x * 100).toFixed(2)}% + 10px )`;
            t.style.top = `calc(${(newPos.y * 100).toFixed(2)}% - 10px )`;
        });
    // camera.rotation.x = 1.16;
    // camera.rotation.y = -0.12;
    // camera.rotation.z = 0.27;
    // const cameraInitPos = { x: 0, y: 200, z: 450 };
    // camera.lookAt(0, -40, -20);
    //const cameraInitPos = { x: 0, y: 150, z: 400 };
    const cameraInitPos = {
        x: 0,
        y: isMobile ? -100 : 0,
        z: isMobile ? 700 : 500
    };
    let targetBox = scene.getObjectByName("cameraTarget");
    let targetPos = new THREE.Vector3(0, 0, 0);
    let tPos = targetBox ? targetBox.position : new THREE.Vector3(0, 0, 0);
    camera.lookAt(tPos);

    if (step === 0 ) {
        // tweenCamera(camera, cameraInitPos);
    } else if (step === 11) {
        tweenCamera(camera, { x: 0, y: 30, z: 250 });
    } else {
        // camTarget = new THREE.Vector3(0, 50, 0);
        // console.log("_______ change camera: ", {
        //     x: 0,
        //     y: 0,
        //     z: isMobile ? 1200 : 500
        // });
        tweenCamera(camera, cameraInitPos);
    }

    // useEffect(()=>{
    //     if(step===11 && (clickedIdx===0 || clickedIdx>0) ){
    //         tweenCamera(camera, { x: 0, y: 30, z: 250 }, targetBox, targetPos);
    //     }
    // }, [clickedIdx])

    camera.updateProjectionMatrix();

    useFrame(state => {
        TWEEN.update();
        camera.lookAt(targetPos);

        if (+step === 0  || +step === 11) {
            theta += 0.02;
            camera.position.x = 252 * Math.sin(THREE.MathUtils.degToRad(theta));
            camera.position.z = 252 * Math.cos(THREE.MathUtils.degToRad(theta));
        }
    });

    return null;
};

function tweenCamera(camera, cameraPos) {    
    let pos = camera.position;
    // let tPos = new THREE.Vector3(0, 0, 0);

    new TWEEN.Tween(pos)
        .to({ x: cameraPos.x, y: cameraPos.y, z: cameraPos.z }, 1000)
        .easing(TWEEN.Easing.Quadratic.Out)
        .onUpdate(function() {})
        .start();

    // new TWEEN.Tween(tPos)
    //     .to({ x: targetPos.x, y: targetPos.y, z: targetPos.z }, 1000)
    //     .easing(TWEEN.Easing.Quadratic.Out)
    //     .onUpdate(function() {
    //         // console.log('tweening camera, xyz: ',pos.x, pos.y, pos.z )
    //     })
    //     .start();
}



export default UpdateCamera;
