const RainShader = {
    vertexShader: `
    #define PI 3.14159265359
    uniform float uTime;
    uniform float uHighlight;
    uniform float uOldTime;
    uniform float uNowTime;
    uniform float uStartTime;
    uniform float uLastHighlight;
    uniform float uHoverIdx;
    uniform float uIsMobile;
    attribute vec3 aSpeed;
    attribute vec3 aEndPosition;
    attribute vec2 aRandom;

    vec3 mod289(vec3 x) {
      return x - floor(x * (1.0 / 289.0)) * 289.0;
    }

    vec2 mod289(vec2 x) {
      return x - floor(x * (1.0 / 289.0)) * 289.0;
    }

    vec3 permute(vec3 x) {
      return mod289(((x*34.0)+1.0)*x);
    }

    float snoise(vec2 v){
      const vec4 C = vec4(0.211324865405187,  // (3.0-sqrt(3.0))/6.0
                          0.366025403784439,  // 0.5*(sqrt(3.0)-1.0)
                        -0.577350269189626,  // -1.0 + 2.0 * C.x
                          0.024390243902439); // 1.0 / 41.0
    // First corner
      vec2 i  = floor(v + dot(v, C.yy) );
      vec2 x0 = v -   i + dot(i, C.xx);

    // Other corners
      vec2 i1;
      //i1.x = step( x0.y, x0.x ); // x0.x > x0.y ? 1.0 : 0.0
      //i1.y = 1.0 - i1.x;
      i1 = (x0.x > x0.y) ? vec2(1.0, 0.0) : vec2(0.0, 1.0);
      // x0 = x0 - 0.0 + 0.0 * C.xx ;
      // x1 = x0 - i1 + 1.0 * C.xx ;
      // x2 = x0 - 1.0 + 2.0 * C.xx ;
      vec4 x12 = x0.xyxy + C.xxzz;
      x12.xy -= i1;

    // Permutations
      i = mod289(i); // Avoid truncation effects in permutation
      vec3 p = permute( permute( i.y + vec3(0.0, i1.y, 1.0 ))
        + i.x + vec3(0.0, i1.x, 1.0 ));

      vec3 m = max(0.5 - vec3(dot(x0,x0), dot(x12.xy,x12.xy), dot(x12.zw,x12.zw)), 0.0);
      m = m*m ;
      m = m*m ;
      vec3 x = 2.0 * fract(p * C.www) - 1.0;
      vec3 h = abs(x) - 0.5;
      vec3 ox = floor(x + 0.5);
      vec3 a0 = x - ox;
      m *= 1.79284291400159 - 0.85373472095314 * ( a0*a0 + h*h );

    // Compute final noise value at P
      vec3 g;
      g.x  = a0.x  * x0.x  + h.x  * x0.y;
      g.yz = a0.yz * x12.xz + h.yz * x12.yw;
      return 130.0 * dot(m, g);
    }

    vec3 getCurvePosition(float posX, float posY, float posZ){
        float noiseScale = 0.005;
        float newPosY = snoise( vec2( posX * noiseScale + ( uTime/10.) , posZ * noiseScale+ ( uTime/10. ) ) );
        vec3 newPos = vec3(0.);
        newPos.x = posX + aRandom.x *5.;
        newPos.z = posZ + sin(aRandom.y) *5.+100.*uIsMobile;
        newPos.y = newPosY * 30.;
        return newPos;
    }

    void main() {
        float fullAnimeTime = 15.0;
        float posX = position.x;
        float posY = position.y;
        float posZ = position.y;
        vec4 mvPosition = vec4(0.);
        float timeChanged = uTime*2.;
        float timer = smoothstep( 0.0, 2.0, timeChanged);
        vec3 newPos = getCurvePosition(posX, posY, posZ);
        float xPos_start = -250.;
        vec3 transformed = vec3(newPos.x, newPos.y+120., newPos.z-80.);
        vec3 transformedNew = transformed.xyz;
        float padding = 5.0;
        vec4 finalFinalPos = vec4(0.);
        float stepTime = (uOldTime - uStartTime)*0.001;
        float yPadding = aSpeed.z/10.;
        vec3 endPoint = vec3(0.);
        float barY = (uIsMobile>0.)? -350.: -265.;
        // aSpeed: x:index, y: rank, z:newDeathsPerWeek

        if(uHighlight == 0.){
          vec3 finalPos = mix(position, newPos, timer);
          // vec3 newEndPos = vec3(aEndPosition.x, aEndPosition.y+6.0* sin(uTime+aRandom.y*6.),aEndPosition.z);
          // finalFinalPos = vec4(aEndPosition, 1.0);
          finalFinalPos = vec4(finalPos, 1.0);
        } else if(uHighlight <= 10. ){
          // the first 11 particles have special pace
          if( aSpeed.x < uLastHighlight ){
            // old dots fall into a barchart 
            transformedNew.z = -(aRandom.y * yPadding)-50.; 
            transformedNew.x = xPos_start + aSpeed.y * padding + aRandom.x*2.;
            transformedNew.y = barY;
          } else if(aSpeed.x >= uLastHighlight && aSpeed.x < uHighlight){
            float timer1_1 = smoothstep( 0., fullAnimeTime*0.3 +aSpeed.x*.3, stepTime ); 
            float timer1_2 = smoothstep( fullAnimeTime-1.0-aRandom.y, fullAnimeTime, stepTime ); 
            vec3 endPoint = vec3( 400.* (aRandom.x-.5), -50.-200.*aRandom.y, 0.);
            if( stepTime-fullAnimeTime+1.0+aRandom.y <= 0.){ 
              transformedNew = mix(transformed, endPoint, timer1_1);
            } else {
              transformedNew = mix(endPoint, vec3((xPos_start + aSpeed.y * padding + aRandom.x*2.), barY, -(aRandom.y * yPadding)-50.), timer1_2);
            }
          }
          finalFinalPos = vec4( transformedNew, 1.0 );
        } else if(uLastHighlight == 33155.){
          // The last step
          // vec3 newEndPos = vec3(aEndPosition.x, aEndPosition.y+6.0* sin(uTime+aRandom.y*6.),aEndPosition.z);
          float timer1_2 = smoothstep( 0.0, fullAnimeTime*0.2, stepTime ); 
          transformedNew = mix(vec3( xPos_start + aSpeed.y * padding + aRandom.x*2., barY, -( aRandom.y * yPadding)-50.), aEndPosition, timer1_2);
          finalFinalPos =  vec4( transformedNew, 1.0 );
        } else { 
          if(aSpeed.x < uLastHighlight){
            // old dots fall into a barchart
            transformedNew.z = -(aRandom.y * yPadding)-50.; 
            transformedNew.x = xPos_start + aSpeed.y * padding + aRandom.x*2.;
            transformedNew.y = barY;
          } else if(aSpeed.x >= uLastHighlight && aSpeed.x < uHighlight){
            if( aSpeed.x+3. >= uHighlight ){
              float timer1_1 = smoothstep( 0., fullAnimeTime*0.3 +0.4*(aSpeed.x+3.-uHighlight) , stepTime ); 
              float timer1_2 = smoothstep( fullAnimeTime-1.0, fullAnimeTime, stepTime ); 
              vec2 point3 = (aSpeed.x+1.==uHighlight)?  vec2(-20., -80.): ((aSpeed.x+2.==uHighlight)? vec2(-120., -130.): vec2(60, -130.));
              vec3 endPoint = vec3(point3, 0.);
              if( stepTime-fullAnimeTime+1.0 <= 0.){ 
                transformedNew = mix(transformed, endPoint, timer1_1);
              } else {
                transformedNew = mix(endPoint, vec3((xPos_start + aSpeed.y * padding + aRandom.x*2.), barY, -(aRandom.y * yPadding)-50.), timer1_2);
              }
            } else {
              // new dots do animation
              float timer2 = smoothstep( (fullAnimeTime - 4.0 - mod( aSpeed.y, 12.) *.5) * aRandom.x, fullAnimeTime-1.0-aRandom.y, stepTime); 
              float timer3 = smoothstep( fullAnimeTime-1.0- aRandom.y, fullAnimeTime, stepTime ); 
              vec3 endPoint = vec3(transformed.x, xPos_start-mod(aSpeed.y, 12.)*100.-aRandom.y*100., transformed.z);
              if( (uOldTime - uStartTime)*0.001 -fullAnimeTime+1.0+aRandom.y <= 0.){
                // the first 8-9 sec -> rain drop
                transformedNew = mix(transformed, endPoint, timer2);
              } else {
                // the later 9-10 sec -> move to bars
                transformedNew = mix(endPoint, vec3( (xPos_start+ aSpeed.y * padding + aRandom.x*2.), barY ,-( aRandom.y * yPadding)-50. ), timer3);
              }
            }
          } 
            finalFinalPos =  vec4( transformedNew, 1.0 );
        }

        mvPosition = modelViewMatrix * finalFinalPos;
        gl_Position = projectionMatrix * mvPosition;
        gl_PointSize =  1500.0 / -mvPosition.z  * max(abs(sin(uTime*2. +posY)), 0.4) + 3. + ((aSpeed.x <11.0|| (aSpeed.x>11.0 && (aSpeed.x+3.==uHighlight || aSpeed.x+1.==uHighlight || aSpeed.x+2.==uHighlight ) ) )? 8.: 2. );
        // gl_PointSize =  1500.0 / -mvPosition.z * abs(sin(uTime*0.4+aRandom.x*10.)) + 3. + ((aSpeed.x <11.0|| (aSpeed.x>11.0 && (aSpeed.x+3.==uHighlight || aSpeed.x+1.==uHighlight || aSpeed.x+2.==uHighlight ) ) )? 8.: 2. );
    }
  `,
    fragmentShader: `
    uniform float uHighlight;
    uniform float uLastHighlight;

    float map(float value, float oldMin, float oldMax, float newMin, float newMax) {
        return newMin + (newMax - newMin) * (value - oldMin) / (oldMax - oldMin);
    }
    float rand(vec2 co){
      return fract(sin(dot(co.xy ,vec2(12.9898,78.233))) * 43758.5453);
    }
    void main() { 
        //0, 180, 216, 0.07, 0.41, 0.59 // 19, 143, 189
        // vec3 color = vec3(0.07, 0.56, 0.64+ .5* rand(gl_PointCoord.xy*2.0) );
        vec3 color = vec3(rand(gl_PointCoord.xy*2.)*.7, 0.56, 0.74 + .5* rand(gl_PointCoord.xy*2.0) );
        vec2 toCenter = (gl_PointCoord.xy - 0.5) * 4.0;
        float len = length(toCenter);
        float a = 1.0 - len;
        gl_FragColor = vec4(color, (uLastHighlight==33155.0)? a*0.6: a);

    }
  `
};

