import React, { useEffect, useRef } from 'react'
import data from '../data/nj_parsed_weekly_death_data.json'
import NumberCount from './NumberCount';

function DeathTollDiv(props){
    const { currentStepIndex } = props;
    const {stepPoint} = data;

    let arr = stepPoint.map(t=> t.num)
    const spanRef = useRef(null);

    // console.log('deathtoll data: ', arr);
    useEffect(()=>{
        const curSpan = spanRef.current;
        let curStepNum = !currentStepIndex? 0: arr[currentStepIndex];
        let lastStepNum = !currentStepIndex ? 0 : arr[currentStepIndex - 1];
        NumberCount(
            "deathToll_num",
            lastStepNum,
            curStepNum,
            currentStepIndex < 3 ? 3000 : 9500
        );

    }, [currentStepIndex]);

    return (
        <div className={`deathTollCtn ${currentStepIndex > 0 ? "active" : ""}`}>
            <span className="deathToll_label">DEATH TOLL</span>
            <br />
            <span
                ref={spanRef}
                className="deathToll_num"
                id="deathToll_num"
                data-target={arr[currentStepIndex]}
                style={{ fontSize: "20px" }}
            >
                {!currentStepIndex? 0: arr[currentStepIndex-1]}
            </span>
        </div>
    );
}

export default DeathTollDiv