import React, { useRef, forwardRef } from "react";
import createMarkup from "./createMarkup";

function CreditCard(props, ref){
    const {data} = props;


    function onClick(){
        document.querySelector(".credit-page").classList.remove("active");
    }

    return (
        <div className="credit-page" ref={ref}>
            <div className="close-credit" onClick={onClick}></div>
            {data.map( (t,i) => (
                <p
                    className="credit_line"
                    key={`credit_line_${i}`}
                    dangerouslySetInnerHTML={createMarkup(t.value)}
                ></p>
            ))}
        </div>
    );  
}

export default forwardRef(CreditCard);