import React, {useRef, useEffect, useState} from "react";
import { animationTime } from "./data.js";
import {select} from 'd3-selection';
import { transition } from 'd3-transition';
import {easeLinear} from 'd3-ease';

function SoundControl(props) {
    const { step, onSouncControlClick, audioEnabled } = props;
    const circleRef = useRef();
    const [inHover, setHover] = useState(false);

    useEffect(()=>{
        const aTime = animationTime[step];
        const cur_Circle = circleRef.current;
        if(step==null) return
        // change the stroke
        select(cur_Circle)
            .attr("stroke-dashoffset", -120)
            .transition()
            .duration(aTime * 1000 - 150)
            .ease(easeLinear)
            // make the duration 50ms less, to leave time for it to finish
            // otherwise the second would play before the first ends, which cancel out the first
            .attr("stroke-dashoffset", 0);

    }, [step])

    return (
        <div
            className={`sound_control_ctn ${step == null ? "" : "active"}`}
            onClick={onSouncControlClick}
            onMouseEnter={() => setHover(true)}
            onMouseLeave={() => setHover(false)}
        >
            <div className={`sound_control_tooltip ${inHover ? "active" : ""}`}>
                {audioEnabled ? "Without audio" : "With audio"}
            </div>
            <svg
                width="40"
                height="40"
                viewBox="0 0 40 40"
                fill="none"
                stroke="currentColor"
                strokeWidth="2"
                strokeLinecap="round"
                strokeLinejoin="round"
                className="feather feather-volume-2"
            >
                <circle
                    cx="20"
                    cy="20"
                    r="19"
                    strokeDasharray="120"
                    strokeDashoffset="-120"
                    ref={circleRef}
                    className="sound_control_circle"
                />
                <circle cx="20" cy="20" r="19" style={{ opacity: 0.3 }} />
                <polygon
                    transform="translate(8, 8)"
                    points="11 5 6 9 2 9 2 15 6 15 11 19 11 5"
                />
                {!audioEnabled && (
                    <path
                        transform="translate(8, 8)"
                        d="M19.07 4.93a10 10 0 0 1 0 14.14M15.54 8.46a5 5 0 0 1 0 7.07"
                    />
                )}
                {audioEnabled && (
                    <g transform="translate(8, 8)">
                        <line x1="23" y1="9" x2="17" y2="15" />
                        <line x1="17" y1="9" x2="23" y2="15" />
                    </g>
                )}
            </svg>
        </div>
    );
}

export default SoundControl;
